export default {
  data () {
    return {
      address: uni.getStorageSync('address') || {},
      goodsList: []
    }
  },
  onLoad (options) {
    //先拿到goodsId，然后发请求
    this.goodsId = options.goodsId
    this.queryGoodsList()
  },
  computed: {
    // 总价
    totalPrice () {
      return this.goodsList.reduce((sum, item) => {
        return sum + item.num * item.goods_price
      }, 0)
    },
    //  详细的收货地址
    consigeeAddr () {
      let { provinceName, cityName, countyName, detailInfo, userName, telNumber } = this.address
      return provinceName + cityName + countyName + detailInfo + ' ' + userName + ' ' + telNumber
    }
  },
  methods: {
    pay () {
      // 如果未选择地址，提示
      if (!this.address.userName) {
        uni.showToast({
          title: '请选择收货地址', //提示的内容,
          icon: 'none',
        });
        return
      }
      // // 如果未登录提示
      // this.token = uni.getStorageSync('token');
      // if (!this.token) {
      //   uni.navigateTo({ url: '/pages/login/login' });
      //   return
      // }
      // 否则创建订单
      this.createOrder()
    },
    // 创建订单
    async createOrder () {
      let data = await this.$request({
        url: '/api/public/v1/my/orders/create',
        method: 'POST',
        isAuth: true,
        data: {
          order_price: this.totalPrice,
          consignee_addr: this.consigeeAddr,
          goods: this.filterGoods()
        }
      })
      this.orderNumber = data.order_number
      this.doPay()
      // 订单创建成功,把购物车里面勾选的商品去掉
      !this.goodsId && this.arrangeCart()
    },
    //订单创建成功,把购物车里面勾选的商品去掉
    arrangeCart () {
      this.$store.commit('arrangeCart')
    },
    async doPay () {
      let data = await this.$request({
        url: '/api/public/v1/my/orders/req_unifiedorder',
        method: 'POST',
        isAuth: true,
        data: {
          order_number: this.orderNumber
        }
      })
      uni.requestPayment({
        ...data.pay,
        success (res) {
          uni.showToast({
            title: '支付成功',
            icon: 'success',
            success () {
              wx.navigateTo({ url: '/pages/order_result/order_result' });
            }
          });
        },
        fail:()=> {
          uni.showToast({
            title: '支付失败',
            icon: 'success',
            success:()=> {
              wx.navigateTo({ url: '/pages/order_result/order_result?orderNumber=' + this.orderNumber });
            }
          });
        }
      })
    },
    filterGoods () {
      return this.goodsList.map(item => {
        return {
          goods_id: item.goods_id,
          goods_number: item.num,
          goods_price: item.goods_price
        }
      })
    },
    // 过滤掉购物车中未选中的商品
    filterCart (cart) {
      return cart.filter(item => {
        return item.checked
      })
    },
    async queryGoodsList () {
      // 根据购物车数据（goodsId）去发请求
      let cart = this.$store.state.cart
      //过滤掉购物车中未选中的商品
      cart = this.filterCart(cart)

      // 当我有goodsId传参的时候，我就构造一个cart,下边逻辑不用改
      if (this.goodsId) {
        cart = [{
          goodsId: parseInt(this.goodsId),
          num: 1,
          checked: true
        }]
      }

      if (!cart.length) {
        return
      }

      let idsArr = cart.map(item => {
        return item.goodsId
      })
      let idsStr = idsArr.join(',')

      let _goodsList = await this.$request({
        url: `/api/public/v1/goods/goodslist?goods_ids=${idsStr}`
      })

      // goodsList和cart数据合并
      this.goodsList = cart.map(item => {
        // 从goodlist找到goods_id为item.goodsId的对象
        let goods = _goodsList.find(v => {
          return v.goods_id === item.goodsId
        })
        return { ...item, ...goods }
      })
    },
    // 发起通讯地址授权
    getAddr () {
      /*
      判断是否拒绝
        1.非拒绝，发起授权，调接口
        2.拒绝的话，弹框提示允许权限
        3.如何判断是否拒绝 uni.getSetting()
      */
      //如何判断是否拒绝 wx.getSetting()
      uni.getSetting({        success: settingRes => {
          console.log(settingRes)
          if (settingRes.authSetting['scope.address'] === false) {
            // 拒绝的话，弹框提示允许权限
            uni.showModal({
              title: '授权',
              content: '请允许通讯地址权限',
              success: res => {
                if (res.confirm) {
                  uni.openSetting()
                }
              }
            });
          } else {
            // 非拒绝，发起授权，调接口
            uni.authorize({
              scope:
                'scope.address',
              success: () => {
                uni.chooseAddress({
                  success: (res) => {
                    // console.log(res)
                    this.address = res
                    uni.setStorageSync('address', res)
                  }
                });
              }
            });
          }
        }      });
    }
  }
}
